
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 48px;
}
.shopcart .content {
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
  font-size: 0;
  color: rgba(255,255,255,0.4);
}
.shopcart .content .content-left {
  -ms-flex: 1;
      flex: 1;
}
.shopcart .content .content-left .logo-wrap {
  display: inline-block;
  vertical-align: top;
  position: relative;
  top: -10px;
  margin: 0 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #141d27;
}
.shopcart .content .content-left .logo-wrap .logo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  text-align: center;
  background: #2b343c;
}
.shopcart .content .content-left .logo-wrap .logo.highlight {
  background: #00a0dc;
}
.shopcart .content .content-left .logo-wrap .logo .icon-shopping_cart {
  line-height: 44px;
  font-size: 24px;
  color: #80858a;
}
.shopcart .content .content-left .logo-wrap .logo .icon-shopping_cart.highlight {
  color: #fff;
}
.shopcart .content .content-left .logo-wrap .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border-radius: 16px;
  font-size: 9px;
  font-weight: 700;
  color: #fff;
  background: #f01414;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
}
.shopcart .content .content-left .price {
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  line-height: 24px;
  padding-right: 12px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255,255,255,0.1);
  font-size: 16px;
  font-weight: 700;
}
.shopcart .content .content-left .price.highlight {
  color: #fff;
}
.shopcart .content .content-left .desc {
  display: inline-block;
  vertical-align: top;
  margin: 12px 0 0 12px;
  line-height: 24px;
  font-size: 10px;
}
.shopcart .content .content-right {
  -ms-flex: 0 0 105px;
      flex: 0 0 105px;
  width: 105px;
}
.shopcart .content .content-right .pay {
  height: 48px;
  line-height: 48px;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
}
.shopcart .content .content-right .pay.not-enough {
  background: #2b333b;
}
.shopcart .content .content-right .pay.enough {
  background: #00b43c;
  color: #fff;
}
.shopcart .shopcart-list {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  transform: translate3d(0, -100%, 0);
}
.shopcart .shopcart-list .list-header {
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  background: #f3f5f7;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.shopcart .shopcart-list .list-header .title {
  float: left;
  font-size: 14px;
  color: #07111b;
}
.shopcart .shopcart-list .list-header .empty {
  float: right;
  font-size: 12px;
  color: #00a0dc;
}
.shopcart .shopcart-list .list-content {
  padding: 0 18px;
  max-height: 217px;
  overflow: auto;
  background: #fff;
}
.shopcart .shopcart-list .list-content .food {
  position: relative;
  padding: 12px 0;
  box-sizing: border-box;
  borde-bottom: 1px solid rgba(7,17,27,0.1);
}
.shopcart .shopcart-list .list-content .food .name {
  line-height: 24px;
  font-size: 14px;
  color: #07111b;
}
.shopcart .shopcart-list .list-content .food .price {
  position: absolute;
  right: 90px;
  bottom: 12px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
}
.shopcart .shopcart-list .list-content .food .cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom: 6px;
}


