}
.header .bulletin-wrap .icon-keyboard_arrow_right {
  position: absolute;
  font-size: 10px;
  right: 12px;
  top: 8px;
}
.header .fade-enter-active,
.header .fade-leave-active {
  transition: opacity 0.5s;
}
.header .fade-enter,
.header .fade-leave-active {
  opacity: 0;
}
